@import url(https://fonts.googleapis.com/css?family=Open+Sans);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

*:focus {
    outline: none;
}
a {
    all: unset;
    font-size: inherit;
    font-weight: inherit;
    font-family: -apple-system, BlinkMacSystemFont, sans-serif;
    font-weight: bold;
    border: none;
    text-align: center;
    text-decoration: none;
    display: inline-block;
}

.AccordionTitle{
  font-size: 35px;
  color: #9E9E9E;
}
.AccordionTitle:hover{
  color: white;
}

.listText{
  color: white;
}

.ListContainer{
  max-width: 500px;
  -webkit-align-self: center;
          align-self: center;
}


.projTitle{
  font-family: 'Open Sans', sans-serif;

  text-align: center;
  font-weight: bold;
  font-size: 50px;
}

a:hover
{
     color:# inherit;
     text-decoration:none;
     cursor:pointer;
}

.wrapper {
    display:-webkit-flex;
    display:flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    height: 100vh;         /* Add this */

}


.titleContainer{

    padding-top: 18%;
    text-align: center;

}

.projCardContainer{

    padding-top: 10%;
    text-align: center;
    -webkit-align-items: center;
            align-items: center;

}

.buttonSection{
    padding-top: .5%;
}

.buttons{
    text-align: center;

}

.bottom{
  -webkit-align-items: flex-end;
          align-items: flex-end;
}

.gitButtons{
  color: white;
}


.mainTitle{
    font-family: -apple-system, BlinkMacSystemFont, sans-serif;
    font-weight: bold;
    font-size: 130px;
}

.subTitle{
    font-family: 'Open Sans', sans-serif;
    font-weight: 500;
    color: black;
}


button{

  border: none;
  text-align: center;
  text-decoration: none;
  display: inline-block;

}

.projectGrid{
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  -webkit-justify-content: space-evenly;
          justify-content: space-evenly;
}

.projectBackground{
  background: #404452;
  top: 0px;
  left: 0px;
  width: 100;
  height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-evenly;
          justify-content: space-evenly;
}

